body {
  height: 100vh;
  justify-content: center;
  display: flex;
}

#input-form {
  width: 30%;
  flex-direction: column;
  align-items: center;
  padding: 3vh 4vw;
  display: flex;
}

#input-form h1 {
  text-align: center;
}

#input-form .bi-asterisk {
  vertical-align: top;
  font-size: .5rem;
}

#input-form .input-step {
  width: 100%;
  flex-direction: column;
  display: flex;
}

#input-form footer {
  width: 30%;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 2vh;
}

#input-form footer #step-action {
  width: 100%;
  justify-content: space-between;
  display: inline-flex;
}

#graph-container {
  width: 70%;
  height: 100%;
  border-right: 4px solid #000;
  position: relative;
}

